.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}
.Entry-wrap {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  margin: 3% auto;
}

.ItemBreed {
  text-align: left;
  list-style: none;
  line-height: 1.9;
  font-size: 20px;
}
.Controls-wrap {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  margin: 3% 7%;
}
.Button,
.Search-Input {
  background-color: #ffffff;
  padding: 15px 22px;
  font-size: 20px;
  border-radius: 10px;
  border-color: #4eadad;
}
.Button:hover {
  border-color: #ffffff;
  background-color: #4eadad;
}
.moreButton {
 -ms-flex-order: 2;
     order: 2;

}

.Search-Input {
}
.ListBreeds {
  -ms-flex: 0 0 100px;
      flex: 0 0 100px;
}
.Dog-Image-wrap {
  -ms-flex: 0 0 50%;
      flex: 0 0 50%;
  padding: 20px;
  height: 500px;
  max-height: 500px;
  position: relative;
      border-radius: 10px;
    -webkit-box-shadow: 2px 4px 13px 0px;
            box-shadow: 2px 4px 13px 0px;
    background: antiquewhite;
        margin-top: 26px;
}



.Dog-Image {
  position: absolute;
  left: 0;
  right: 0;
  top: 50%;
  -webkit-transform: translateY(-50%);
      -ms-transform: translateY(-50%);
          transform: translateY(-50%);
  max-width: calc(100% - 20px);
  height: auto;
  width: auto;
  max-height: calc(500px - 40px);
  margin: auto;
  display: block;
}
@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}



